<template>
    <div class='basics'>
        <h1>
            <vgl-wave text="联系我"></vgl-wave>
        </h1>
           <transition
     transition
       :duration="{ enter: 2500, leave: 1500 }"
      enter-active-class="animated fadeInRight">
        <el-card class="box-card" v-if='show'>
            <div class='aboutme'>
                <h3>自我评价</h3>
                <p v-for='(item,index) in msg' :key='index' v-text='item.message'></p>
            </div>
            <div class='hobby'>
                <h3>兴趣爱好</h3>
                <p>爱好篮球/羽毛球</p>
            </div>
            <div class='look'>
                <h3>常看</h3>
                <el-row :gutter="20" style='margin:10px auto;width:50%'>
                    <el-col :span='6' v-for='(item,index) in links' :key='index'>
                        <div class="grid-content">
                            <a :href="item.link" :class='item.icon' class='fa fa-2x'>
                              <p v-text='item.msglink'></p>
                            </a>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <div class='contact'>
                <h3>联系我</h3>
                <p>
                    <span class='fa fa-mobile fa-1x'>13726014889</span>
                </p>
                <p>
                    <span class='fa fa-envelope-open fa-1x'>13726014889@139.com</span>
                </p>
            </div>
        </el-card>
        </transition>
    </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      msg: [
        { message: '愿意虚心向他人学习，懂得感恩,有足够的工作热情' },
        { message: '能快速适应环境,适应加班,责任心强' },
        { message: '善于学习，愿意不断尝试新事物' },
        { message: '良好的沟通和团队协作能力' }
      ],
      links: [
        {
          icon: 'fa-github-square',
          link: 'https://github.com',
          msglink: 'github'
        },
        {
          icon: 'fa-hand-o-left',
          link: 'https://segmentfault.com',
          msglink: 'segmentfault'
        },
        {
          icon: 'fa-hand-o-right',
          link: 'https://www.zhihu.com',
          msglink: '知乎'
        },
        {
          icon: 'fa-link',
          link: 'https://stackoverflow.com/',
          msglink: 'stackoverflow'
        }
      ]
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  }
}
</script>

<style lang='scss' scoped>
.basics {
  h1 {
    color: #fff;
    font-size: 2rem;
  }
  .box-card {
    background-color: rgba(233, 225, 225, 0.5);
    border-radius: 25px;
    .aboutme,
    .hobby,
    .look,
    .contact {
      color: #fff;
      h3 {
        color: #000;
        font-size: 1.4rem;
        line-height: 1.5;
      }
      p {
        line-height: 1.5;
      }
    }
    .look {
      .grid-content {
        .fa {
          color:#fff;
          p {
            font-size: 1rem;
          }
          &:hover{
           color: rgba(13, 73, 87, 0.8);
          }
        }
      }
    }
  }
}
</style>



