<template>
    <div class='basics'>
        <h1><vgl-wave text="掌握技能"></vgl-wave></h1>
        <transition  :duration="{ enter: 2500, leave: 1500 }"
      leave-active-class="animated fadeOutRight"
      enter-active-class="animated fadeInLeft">
        <el-card class="box-card" v-if='show'>
            <el-row :gutter="20" v-for='(item,index) in msg' :key='index'>
                <el-col :span="8">
                    <div class="grid-left" :style="{opacity:`${item.opacity}`}">
                        <h2 v-text='item.name'></h2>
                    </div>
                </el-col>
                <el-col :span="16">
                    <el-row :gutter="10">
                        <el-col :span="12" v-for='(child,index) in item.children' :key='index'>
                            <div class="grid-right" :style="{opacity:`${child.opacity}`}">
                                <h3 v-text='child.name'></h3>
                            </div>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
        </el-card>
        </transition>
    </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      msg: [
        {
          name: '前端',
          opacity: 0.8,
          children: [
            {name: 'Vue', opacity: 0.8},
            {name: 'Vuex', opacity: 0.8},
            {name: 'jquery', opacity: 0.8},
            {name: 'Ajax', opacity: 0.8},
            {name: 'zepto', opacity: 0.8},
            {name: '微信小程序', opacity: 0.8}
          ]

        },
        {
          name: 'ui框架',
          opacity: 0.8,
          children: [
            {name: 'Element-ui', opacity: 0.8},
            {name: 'layui', opacity: 0.8},
            {name: 'Bootstrap', opacity: 0.8},
            {name: 'Mintui', opacity: 0.8},
            {name: 'Mui', opacity: 0.8},
            {name: 'Wepy', opacity: 0.8}
          ]
        },
        {
          name: '版本管理工具',
          opacity: 0.8,
          children: [
            {name: 'git', opacity: 0.8},
            {name: '码云', opacity: 0.8}
          ]
        }
      ]
    }
  },
  methods: {
    timeout () {
      // 异步箭头
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  }
}
</script>

<style lang='scss' scoped>
.basics {
  h1 {
    color: #fff;
    font-size: 2rem;
  }
  .box-card {
    border-radius: 25px;
    color:red;
    .grid-left {
      margin: 0 auto;
      margin-bottom: 5px;
      width: 100%;
      height: 160px;
      line-height: 160px;
      background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
    }
    .grid-right{
        margin-bottom: 8px;
        height: 40px;
        line-height: 40px;
        background-image: linear-gradient(60deg, #64b3f4 0%, #c2e59c 100%);
    }
  }
}
</style>



