<template>
    <div class='basics'>
        <h1>
            <vgl-wave text="项目经历"></vgl-wave>
        </h1>
        <transition
     :duration="{ enter: 2500, leave: 1500 }"
      enter-active-class="animated fadeInRight" >
        <el-card class="box-card" v-if='show'>
            <el-row :gutter="20">
                <el-col :span="12" v-for='(item,index) in msg' :key='index' :xs="24">
                    <div class="grid-content">
                        <h3>项目:{{item.num+'('+item.title+')'}}</h3>
                        <p>开发周期:{{item.time}}</p>
                        <p>项目描述:{{item.describe}}</p>
                        <p>技术描述:{{item.skill}}</p>
                    </div>
                </el-col>
            </el-row>
        </el-card>
        </transition>
    </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      msg: [
        {
          num: 1,
          title: '商城后台管理系统',
          time: '2018.6-2018.9',
          describe: '柚子舍是一个化妆品购物网站，挖掘柚子护肤的美丽力量,该项目主要实现商品管理，商品上下架，用户管理等功能',
          skill: `1.主要负责首页以及登陆页面的编写。
          2.经过讨论，最终项目组决定使用VUE.js来完成此项目。原因为mvvm下的vue确实可以提高开发效                  
           率，便于维护。
          3.利用vue-cli搭建项目实现重构及优化。
          4.利用axios进行数据交互的处理和渲染。
          5.使用vue-router实现页面路由的跳转，使用element-ui进行页面的构建，使用vuex管理数据的     
            存储状态。
          6.利用路由守卫来保持用户的登陆。`
        },
        {
          num: 2,
          title: '哈淘微分销移动APP',
          time: '2018.2-2018.4',
          describe: `项目分为商城首页、商品列表、商品详情、登录注册，购物车等模块。手机端的自适应我们使用media（媒体查询）来实现，商城列表中分为头部、导航、轮播图以及部分商品列表；商品列表的主页面需要实现功能为,当在菜单栏中选择不同的价格区间，后台便要渲染响应的列表出来。`,
          skill: `1.项目采用Mint Ui 构建
           2.用LocalStorge保存重要数据，避免请求重复数据，优化性能，减少网络请求
           3.使用vue框架将项目组件化，方便项目维护升级
           4.利用axios进行数据交互的处理和渲染。
           5.对webpack进行了定制化，将项目进行打包分离
           6.使用vue-router实现页面路由的跳转，使用mint-ui实现图片的轮播`
        },
        {
          num: 3,
          title: '信达雅一起买网 移动端',
          time: '2017.11-2018.1',
          describe: `一起买是一个轻松购物网站，给用户带来最实惠的方便。倡导理性消费,以让用户买到高性比价商品。`,
          skill: `1. 移动端布局（rem、媒体查询），来进行屏幕适配；
          2. 使用Zepto的tap事件避免移动端点击300ms延迟的问题。
          3. 使用swiper插件来实现产品详情页轮播效果
          4. 利用Ajax进行数据交互的处理和渲染。
          5. 使用sass预编译css文件。`
        },
        {
          num: 4,
          title: 'HWA安琦道尔官网',
          time: '2017.8-2017.10',
          describe: `HWA安琦道尔官网，从事城市设计总体规划及景观设计服务的公司。`,
          skill: `1.采用div+css搭建网页，使用bootstrap框架提高开发效率；
             2.轮播图bootstrap框架自带轮播，部分使用animate.css插件， css3动画，提升用户体验；
             3.配合后台工程师，进行代码的调试和bug的修复；
             4.采用ajax，template.js模板进行数据的请求和渲染；`
        }
      ]
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  }
}
</script>

<style lang='scss' scoped>
.basics {
  h1 {
    color: #fff;
    font-size: 2rem;
  }
  .box-card {
    width: 90%;
    .grid-content{
        color:#fff;
        border-radius: 15px;
        text-align: left;
        margin-bottom: 10px;
        padding: 10px;
        background-color: rgba(59, 123, 13, 0.2);
        @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
        }
        &:hover{
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
        }
        h3{
            text-align: center;
            font-weight: 600;
            font-size: 1.6rem;
        }
        p{
            padding: 6px 0;
            line-height: 1.2;
            text-indent: 2em;
            @media screen and (max-width: 768px) {
            text-indent: 12px;
            font-size: 0.8rem;
            line-height: 1.1;
        }
        }
    }
  }
}
</style>



