<template>
    <div class='basics'>
        <h1><vgl-wave text="基本资料"></vgl-wave></h1>
        <transition  :duration="{ enter: 2500, leave: 1500 }" enter-active-class="animated rotateIn">
        <el-card class="box-card" v-if='show'>
            <img class="avatar" src="../assets/timg.jpg" alt="">
            <h2>人生自古何其乐，偷的浮生半日闲</h2>
            <p>我叫余练</p>
            <p>两年web前端开发经验</p>
            <div class='state'>
                <el-row :gutter="30">
                    <el-col :span="6" :xs="12" v-for="(item,index) in states" :key="index">
                        <div class="grid-content">
                            <i class='fa fa-2x' :class="item.icon"></i>
                        </div>
                        <p v-text='item.val'></p>
                    </el-col>
                </el-row>
            </div>
        </el-card>
        </transition>
    </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      states: [
        { icon: 'fa-street-view', val: '年龄/26' },
        { icon: 'fa-graduation-cap', val: '学历/大专' },
        { icon: 'fa-location-arrow', val: '坐标/广州' },
        { icon: 'fa-flag', val: '状态/离职' }
      ]
    }
  },
  methods: {
    timeout () {
      setTimeout(() => {
        this.show = true
      }, 1000)
    }
  },
  mounted () {
    this.timeout()
  }
}
</script>

<style lang='scss' scoped>
.basics {
  h1 {
    color: #fff;
    font-size: 2rem;
  }
  .box-card {
    border-radius: 25px;
    .avatar {
      width: 120px;
      height: 120px;
      border-radius: 50%;
    }
    h2 {
      font-size: 1.6rem;
      font-weight: bold;
      margin: 20px auto;
    }
    p {
      line-height: 1.5;
    }
    .state {
      margin-top: 20px;
      .grid-content {
        width: 80px;
        height: 80px;
        margin: 0px auto;
        border-radius: 50%;
        border: 1px solid rgba(9, 92, 49, 0.5);
        margin: 5px auto;
        background-color: rgba(13, 73, 87, 0.4);
        &:hover {
          background-color: rgba(13, 73, 87, 0.8);
        }
        .fa {
          line-height: 80px;
          color:#fff;
          &:hover{
              font-size: 3.5rem;
          }
        }
      }
      p {
        line-height: 2;
      }
    }
  }
}
</style>



