<template>
  <div class='resume' @mousewheel="mouse" :style="{
    height: `${clientHeight}`
  }">
    <transition :duration="{ enter: 2500, leave: 1500 }" :leave-active-class="'animated ' + leaveanimate" :enter-active-class="'animated ' + enteranimate">
      <div v-if='init===1' class="section" :style="{height: `${clientHeight}`}">
        <div class='sec'>
          <con1></con1>
        </div>
      </div>
    </transition>
    <transition :duration="{ enter: 2500, leave: 1500 }" :leave-active-class="'animated ' + leaveanimate" :enter-active-class="'animated ' + enteranimate">
      <div v-if='init===2' class="section" :style="{height: `${clientHeight}`}">
        <div class='sec'>
          <con2></con2>
        </div>
      </div>
    </transition>
    <transition :duration="{ enter: 1500, leave: 1500 }" :leave-active-class="'animated ' + leaveanimate" :enter-active-class="'animated ' + enteranimate">
      <div v-if='init===3' class="section" :style="{height: `${clientHeight}`}">
        <div class='sec'>
          <con3></con3>
        </div>
      </div>
    </transition>
    <transition :duration="{ enter: 1500, leave: 1500 }" :leave-active-class="'animated ' + leaveanimate" :enter-active-class="'animated ' + enteranimate">
      <div v-if='init===4' class="section" :style="{height: `${clientHeight}`}">
        <div class='sec'>
          <con4></con4>
        </div>
      </div>
    </transition>
    <!-- 下一页 -->
    <div class="next">
      <i class="fa fa-angle-down fa-5x" @click="nextPage"></i>
    </div>
  </div>
</template>

<script>
import con1 from '@/views/Component1.vue'
import con2 from '@/views/Component2.vue'
import con3 from '@/views/Component3.vue'
import con4 from '@/views/Component4.vue'
export default {
  components: {
    con1: con1,
    con2: con2,
    con3: con3,
    con4: con4
  },
  data () {
    return {
      init: 1,
      scrolltime: 0,
      leaveanimate: 'zoomOutUp',
      enteranimate: 'zoomInDown',
      clientHeight: '900px'
    }
  },
  methods: {
    mouse (event) {
      console.log(event)
      var time = event.timeStamp - this.scrolltime
      // 判断event.deltaY>0 的时候 是向下滚动
      if (time > 1000) {
        // 判断滚动方向进行操作
        if (event.deltaY > 0) {
          console.log('down')
          if (this.init === 4) {
            this.init = 1
          } else {
            this.init = this.init + 1
          }
        } else {
          console.log('up')
          if (this.init === 1) {
            this.init = 4
          } else {
            this.init = this.init - 1
          }
        }
      } else {
        console.log('请滚动的慢一点')
      }
    },
    // 下一页
    nextPage () {
      if (this.init === 4) {
        this.init = 1
      } else {
        this.init = this.init + 1
      }
    }
  },
  mounted () {
    this.clientHeight = `${document.documentElement.clientHeight}px`
    // 然后监听window的resize事件．在浏览器窗口变化时再设置下背景图高度
    window.onresize = () => {
      this.clientHeight = `${document.documentElement.clientHeight}px`
      console.log(this.clientHeight)
    }
  }
}
</script>

<style lang='scss' scoped>
.resume {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-image: url(./assets/timg1.jpg);
  background-size: contain;
  background-position: -20% 10%;
  .section {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    .sec {
      width: 100%;
    }
  }
  .next {
    // .position-bottom;
    .fa {
      position: fixed;
      bottom: 20px;
      left: 47%;
      color: rgba(4, 10, 41, 0.8);
      opacity: 0.8;
      cursor: pointer;
      animation: myfirst 5s infinite;
      @media screen and (max-width: 768px) {
        font-size: 20px;
      }
    }
  }
  @keyframes myfirst {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
}
</style>



